:root {
  --green: #64ffda;
  --lightest-navy: #233554;
  --lightest-slate: #ccd6f6;
}
#root {
  display: grid;
  min-height: 100vh;
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
}
body {
  margin: 0;
  background-color: #0a192fd9;
  line-height: 1.3;
}
p {
  max-width: 690px;
  margin: 0px;
  font-size: 20px;
  margin-top: 20px;
  color: var(--lightest-slate);
}
.App {
  display: flex;
  justify-content: center;
  color: white;
  align-items: flex-start;
}
header {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: right;
  padding: 10px;
}
h3{
  color: var(--lightest-slate);
}
h2{
  font-size: 40px;
}
li{
  transition-delay: 300ms;
}

