.contact__title{
    padding: 0px;
    margin: 0px;

}
.contact__title::before{
    position: relative;
    counter-increment: section 2;
    content: "0" counter(section) ".";
    margin-right: 10px;
    font-weight: 400;
    font-size: 30px;
    color: var(--green);
}
.email__container{
    margin: 0 auto;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 5px 10px 0 rgba(0, 0, 0, 0.1);
}
.email_click{
    font-size: 30px;
    border: 1px solid var(--green);
    background-color: var(--lightest-navy);
}